*{margin:0px;padding:0px;box-sizing:border-box;font-family:"Open Sans"}.navbar-container{height:50px;width:100%;padding:4% 5% 2.5% 5%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.navbar-container #brand{display:flex;flex-direction:row;justify-content:center;align-items:center;height:100%;width:auto}.navbar-container #brand div{height:100%;width:45px;display:flex;flex-direction:row;justify-content:center;align-items:center}.navbar-container #brand h1{margin-left:15px;font-size:28px}.navbar-container #media-social ul{list-style:none;display:flex;flex-direction:row;justify-content:center;align-items:center}.navbar-container #media-social ul li{display:inline-block;margin-left:10px}.navbar-container #media-social ul li a{text-decoration:none}
.weather{margin:0px 4%;display:flex;flex-direction:column;align-items:center}.weather .temp-icon{display:flex;align-items:center}.weather .temp-icon .temp-info{padding:5px}.weather .temp-icon .temp-info .location{display:flex;align-items:center}.weather .temp-icon .temp-info .location h3{font-size:16px;margin-left:10px}.weather .temp-icon .temp-info .temp{display:flex;align-items:center}.weather .temp-icon .temp-info .temp h1{font-size:72px;margin:0px 5px}.weather .temp-icon .temp-info .temp #button-container{display:flex;flex-direction:column;align-items:center}.weather .temp-icon .temp-info .temp #button-container #button{font-size:18px;font-weight:600;max-width:30px;height:30px;padding:0px;margin-top:5px}.weather h1{font-size:32px;font-weight:600}
*{margin:0px;padding:0px;box-sizing:border-box;font-family:"Open Sans"}.acrylic{position:relative;z-index:1;padding:10px 15px;background-color:#fff;-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);opacity:1;font-size:14px;overflow:hidden}.acrylic::before,.acrylic::after{content:"";position:absolute;top:0px;left:0px;width:100%;height:100%}.acrylic::before{z-index:-1;background-color:#fff;opacity:.1;mix-blend-mode:exclusion}.acrylic::after{z-index:0;background-image:url(/weather-app/static/media/noise.84b285bf.png);background-repeat:no-repeat;background-size:auto 100%;box-shadow:inset 0px 0px 3px 1px rgba(0,0,0,.4);opacity:.3}.acrylic:hover{opacity:1;transform:scale(1.1);cursor:pointer;transition:all 250ms cubic-bezier(0, 0.995, 0.99, 1) !important;-webkit-transition:all 250ms cubic-bezier(0, 0.995, 0.99, 1) !important}.light{background-color:#fff}.dark{background-color:#000;color:#fff}.shadow{box-shadow:0 3px 4px 0 rgba(0,0,0,.14),0 3px 3px -2px rgba(0,0,0,.12),0 1px 8px 0 rgba(0,0,0,.2)}.o50{opacity:.5}.o55{opacity:.55}.o60{opacity:.6}.o65{opacity:.65}.o70{opacity:.7}.o75{opacity:.75}.o80{opacity:.8}.o85{opacity:.85}.o90{opacity:.1}.r5{border-radius:5px}.r10{border-radius:10px}.r15{border-radius:15px}.r20{border-radius:20px}.r25{border-radius:25px}.r50{border-radius:50px}
.mini-card{margin:2% 4%;padding:0% 20%;display:flex;align-items:center;justify-content:center;flex-wrap:wrap}@media(max-width: 1684px){.mini-card{padding:0% 13%}}
.forecast{display:flex;align-items:center;justify-content:space-between}.forecast h2{font-size:22px;font-weight:600}.forecast .temp{display:flex;align-items:flex-end}.forecast .temp #max{font-size:20px;margin-right:10px}.forecast .temp h4{font-size:16px}.forecast h3{text-transform:capitalize;font-size:14px}
*{margin:0px;padding:0px;box-sizing:border-box;font-family:"Open Sans"}.footer{position:absolute;bottom:2%;width:100%;padding:0% 4%;display:flex;justify-content:center;align-items:center}.footer ul{list-style:none}.footer ul li{display:inline-block;margin-right:15px}.footer ul li div{display:flex;align-items:center}.footer ul li div #icon{color:#fff;font-size:16px}.footer ul li div a{text-decoration:none;color:#fff;margin-left:5px;font-size:15px}.footer ul li:hover{transform:scale(1.1);transition:all 250ms cubic-bezier(0, 0.995, 0.99, 1) !important;-webkit-transition:all 250ms cubic-bezier(0, 0.995, 0.99, 1) !important}
.main-container{background-repeat:no-repeat;background-size:cover;width:100vw;height:100vh}.main-container .daily-forecast{margin:1% 4%}.main-container .daily-forecast h1{font-size:36px;font-weight:800;margin-bottom:2%;color:#fff}.loading{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}
